<template>
    <div>
        <div class="Home-Title">
            <i class="el-icon-chat-dot-square"></i>
            世界上最好的语言
        </div>
        <Done :State="State" />
        <Choosing :State="State" @Finish="Done" />
    </div>
</template>

<script>
import Done from './Done'
import Choosing from './Choosing'
const axios = require('axios');
export default {
    name: "Home",
    components: {
        Done,
        Choosing
    },
    data () {
        return {
            State: {
                "Done": false,
                "Name": "#",
                "Location": "#",
                "Href": "#",
                "Submission": true
            }
        }
    },
    methods: {
        Done (SubData) {
            this.State.Done = true;
            this.State.Name = SubData.data.Name;
            this.State.Location = SubData.data.Location;
            this.State.Href = SubData.data.Href;
        }
    },
    mounted () {
        axios.get("/data").then((res) => {
            let thisV = this;
            setTimeout(function(){
                thisV.State = res.data.data;
            }, 1000)
        }).catch(() => {

        })
    }
}
</script>

<style scoped lang="scss">
    .Home-Title{
        font-size: 40px;margin: 30px 0;font-family: "Microsoft YaHei UI Light";
        i{
            font-size: 46px;
            vertical-align: text-bottom;
        }
    }
</style>